body {
  color: #212529;
  background-color: #f8f9fa;
  flex-direction: column;
  min-height: 100vh;
  margin: 0;
  padding: 0;
  font-family: Arial, sans-serif;
  display: flex;
}

main {
  flex-direction: column;
  align-items: flex-start;
  padding-left: 100px;
  padding-right: 50px;
  display: flex;
}

header {
  text-align: center;
  background-color: #fff;
  padding: 20px;
  position: relative;
  box-shadow: 0 2px 5px #0000001a;
}

header h1 {
  color: #007bff;
  margin: 0;
  font-size: 28px;
}

header h5 {
  color: #6c757d;
  margin: 5px 0;
  font-weight: normal;
}

#logout {
  color: #fff;
  cursor: pointer;
  background-color: #dc3545;
  border: none;
  border-radius: 5px;
  padding: 10px 15px;
  font-size: 14px;
  font-weight: bold;
  position: absolute;
  top: 50%;
  right: 20px;
  transform: translateY(-50%);
}

#logout:hover {
  background-color: #c82333;
}

form {
  background: #fff;
  border-radius: 8px;
  width: 100%;
  max-width: 600px;
  margin-bottom: 20px;
  padding: 20px;
  box-shadow: 0 0 10px #0000001a;
}

label {
  color: #212529;
  margin: 10px 0 5px;
  font-weight: bold;
  display: block;
}

input {
  border: 1px solid #ced4da;
  border-radius: 5px;
  width: 100%;
  margin-bottom: 10px;
  padding: 10px;
  font-size: 16px;
}

input:focus {
  border-color: #007bff;
  outline: none;
  box-shadow: 0 0 4px #007bff80;
}

button {
  color: #fff;
  cursor: pointer;
  background-color: #007bff;
  border: none;
  border-radius: 5px;
  margin-top: 10px;
  padding: 12px;
  font-size: 16px;
  font-weight: bold;
}

button:hover {
  background-color: #0056b3;
}

#add-book {
  background: #fff;
  border-radius: 8px;
  width: 100%;
  max-width: 700px;
  margin-bottom: 20px;
  padding: 20px;
  box-shadow: 0 2px 5px #0000001a;
}

#book-list-section {
  background: #fff;
  border-radius: 8px;
  max-width: 600px;
  padding: 20px;
  box-shadow: 0 2px 5px #0000001a;
}

#book-list {
  padding: 0;
  list-style: none;
}

#book-list li {
  background: #e9ecef;
  border-radius: 5px;
  justify-content: space-between;
  align-items: center;
  margin: 8px 0;
  padding: 10px;
  display: flex;
}

#book-list li strong {
  color: #212529;
  font-size: 16px;
}

#book-list .delete-btn {
  background-color: #dc3545;
  padding: 5px 10px;
  font-size: 14px;
}

#book-list .delete-btn:hover {
  background-color: #c82333;
}

#chatbot-container {
  background: #fff;
  border: 2px solid #007bff;
  border-radius: 8px;
  width: 350px;
  padding: 15px;
  position: fixed;
  bottom: 20px;
  right: 20px;
  box-shadow: 0 4px 6px #0000001a;
}

#chat-history {
  background: #f8f9fa;
  border-bottom: 2px solid #007bff;
  border-radius: 5px;
  height: 250px;
  margin-bottom: 10px;
  padding: 10px;
  overflow-y: auto;
}

#chat-input {
  border: 1px solid #007bff;
  border-radius: 5px;
  width: calc(100% - 70px);
  padding: 10px;
  font-size: 14px;
}

#send-btn {
  background-color: #28a745;
  padding: 10px 15px;
  font-size: 14px;
}

#send-btn:hover {
  background-color: #218838;
}

footer {
  text-align: center;
  color: #6c757d;
  background-color: #fff;
  margin-top: auto;
  padding: 15px;
  font-size: 14px;
  box-shadow: 0 -2px 5px #0000001a;
}
